export default "<template>\n  <div\n    style=\"\n      display: flex;\n      flex-direction: row;\n      flex-wrap: wrap;\n    \"\n  >\n    <div\n      v-for=\"flip in flips\"\n      :key=\"flip\"\n      style=\"margin: 10px; text-align: center;\"\n    >\n      <VIcon\n        name=\"check\"\n        :flip=\"flip\"\n      />\n      <div>{{ flip }}</div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport {\n  VIcon\n} from '@/components';\n\nexport default {\n  name: 'IconFlip',\n  components: {\n    VIcon\n  },\n  data () {\n    return {\n      flips: [\n        '-',\n        'horizontal',\n        'vertical',\n        'both'\n      ]\n    };\n  }\n};\n</script>\n";